.subHeading {
  text-align: center;
  text-decoration: underline;
}

#aboutMeContainer {
  margin: 70px 0;
  max-width: 100%;
  min-width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
  border-bottom:solid  #66FF66;

  #aboutMeDiv {
    flex: 1;
    max-width: 1000px;

    #aboutMeText {
      box-sizing: content-box;
      max-width: 100%;
      min-width: 175px;
      text-align: center;
      margin-right: 10px;
    }
  }

  #aboutMeImg {
    flex: 0 0 auto;
    max-width: 484px;
    max-height: 850px;
    border-radius: 5px;
    border: 5px solid #66FF66;
  }

  #skillsSVGContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    // flex-wrap: wrap;

    .skillSVG {
      margin: 10px;
    }
  }

}

#projectsContainer {
  padding-bottom: 20px;
  border-bottom: solid #66FF66;
  #projecsSubHeading {
    margin: 50px 0;
  }
}

#contactMeContainer{
  border-radius: 0;
  // Placeholder ruleset meant for nav bar scroll
}

